// app/plaqueta/page.tsx
import { textosPlaqueta } from './plaqueta';
import AcordeonTextos from './AcordeonTextos';

export default function PlaquetaPage() {
  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="container mx-auto px-4">
        {/* Header alineado con el mismo ancho del acordeón */}
        <header className="max-w-3xl mx-auto">
          <h1 className="text-center mb-12 text-4xl font-bold mb-4">El cielo de Laika</h1>
          <div className="text-right mb-8">
            <p>Es que, cuando te disparan, sangras.</p>
            <p>H. M., <span>Sputnik, mi amor</span></p>
          </div>
        </header>
        
        <AcordeonTextos textos={textosPlaqueta} />
        
        {/* Información de ediciones alineada */}
        <div className="max-w-3xl mx-auto mt-8 text-center text-sm text-gray-600">
          <p>© Santiago Olivo Franco y Victor Laureano Vega, 2024</p>
          <p>Primera edición: enero de 2024</p>
          <p>Segunda edición: enero de 2025</p>
          <br />
          <p>Traducción del epígrafe de Lourdes Porta y Junichi Matsuura</p>
        </div>

        {/* Botón de descarga alineado */}
        <div className="max-w-3xl mx-auto text-center mt-8">
          <a 
            href="El cielo de Laika.pdf" 
            download
            className="inline-flex items-center px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50 bg-white transition-colors"
          >
            Descargar PDF
          </a>
        </div>
      </div>
    </div>
  );
}